<script>
import {
  getData,
  opthionList,
  CompanySwitchClear,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: 'instorageOtheninstoragedetailOtheninstoragedetail',
  data() {
    return {
      outerVisible: false,
      TableData: {
        tableData: [],
        allList: {},
        total: 10
      },
      is_shop: 0,
      tableHeight: 600
    };
  },
  computed: {
    ...mapGetters(["otheninstoragedetailIndexGet"])
  },
  methods: {
    ...mapActions(["otheninstoragedetailIndexACT"]),
    renderHeaderpro() {
      return (
        <el-tooltip class="item" content="Bottom center" effect="light">
          <p slot="content">生产日期列后期将会隐藏，同时新增商品入库日期列</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            生产日期
          </span>
        </el-tooltip>
      );
    },
    allocationInventoryListchange(val) {
      if (val != "") {
        this.params.allocation_name = this.allocationInventoryList.filter(
          obj => obj.allocation_id === val
        )[0].allocation_name;
      }
    },
    businessIdChange(val) {
      if (val != "") {
        this.params.buyer_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    supplierChange(val) {
      if (val != "") {
        this.params.supplier_name = this.supplierList.filter(
          obj => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    departmentChange(val) {
      if (val != "") {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
      this.$set(this.params, "buyer_id", "");
    },
    receivablesModeIdChange(val) {
      if (val != "") {
        this.params.receivables_mode_name = this.receivablesList.filter(
          obj => obj.receivables_mode_id === val
        )[0].receivables_mode_name;
      }
    },
    clearAllocation(val) {
      if (val != "") {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
      this.$set(this.params, "allocation_id", null);
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportOtherInstorageUrl(this.params).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 2) {
          sums[index] = "页小计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 15 || index === 19) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index == 13) {
            sums[index] = sums[index];
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      CompanySwitchClear(this, this.params);
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.defaultArea = [0];
      this.$set(this, "params", {
        auditor_status: 0
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "getOtherInstorageDetailList", //接口名
        vm: this //传递的this
      });
      this.onSearch();
      getData(this.searchParams, 2);
    },
    onSearch() {
      //查询
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.otheninstoragedetailIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
      getData(this.searchParams, 2);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams, 2);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams, 2);
    }
  },
  mounted() {
    // this.authority = JSON.parse(localStorage.getItem("authority"));
    if (
      this.otheninstoragedetailIndexGet &&
      this.otheninstoragedetailIndexGet.params
    ) {
      if (this.otheninstoragedetailIndexGet.params.start_time) {
        const {
          start_time,
          end_time
        } = this.otheninstoragedetailIndexGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }

      Object.assign(this.otheninstoragedetailIndexGet.params, {
        employee_id: this.otheninstoragedetailIndexGet.params.buyer_id,
        employee_name: this.otheninstoragedetailIndexGet.params.buyer_name
      });
      this.$set(this, "params", this.otheninstoragedetailIndexGet.params);
      opthionList(this, this.otheninstoragedetailIndexGet.params);
    }
    // this.$set(this, "params", {
    //   auditor_status: 0
    // });
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getOtherInstorageDetailList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams, 2, tableResize.bind(this));
    //取消单据右侧滚动条
    window.onresize = tableResize.bind(this);

    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
  }
};
</script>
